body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.appBar {
  flex-grow: 1; }

.userAvatar {
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
  margin: 0 .5vw; }

.paper {
  padding-top: 5vh;
  padding-bottom: 5vh;
  max-width: 100vw; }

.dropdown {
  max-width: 96.4vw;
  width: 640px;
  margin: 0 1.8vw; }

.resultTable {
  max-width: 94.4vw;
  margin: 1vh 1.8vw;
  overflow: hidden; }
  .resultTable th,
  .resultTable td {
    border: solid 1px #cccccc;
    border-collapse: collapse; }
  .resultTable .checked {
    background-color: #9ac17a; }
  .resultTable .checking {
    background-color: #eaaddc; }
  .resultTable .in_progress {
    background-color: #e5e16b; }
  .resultTable .todo {
    background-color: #c7ced8; }
  .resultTable .not_done {
    background-color: #d62020; }

.landingContent .rssLogo {
  max-width: 45vw;
  max-height: 45vw;
  height: 240px; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

a {
  text-decoration: none;
  color: #515356; }

.fas {
  margin: 0 .5em; }

