.resultTable {
    max-width: 94.4vw;
    margin: 1vh 1.8vw;
    overflow: hidden;
  th,
  td {
    border: solid 1px rgb(204, 204, 204);
    border-collapse: collapse;
  }
  .checked {
    background-color: #9ac17a;
  }
  .checking {
    background-color: #eaaddc;
  }
  .in_progress {
    background-color: #e5e16b;
  }
  .todo {
    background-color: #c7ced8;
  }
  .not_done {
    background-color: #d62020;
  }
}
